import React from 'react'
import * as d3 from 'd3'

export default class NFLViz extends React.Component{

  constructor(props){
    super(props)

    this.setContext = this.setContext.bind(this)
  }

  componentDidMount(){
    //Get data from external source if needed
    //Create the SVG canvas
    this.setContext();
  }

  componentDidUpdate(){
    //Remove and redraw the canvas
    d3.select(this.refs.nflchart).remove()
    d3.select(this.refs.tooltip).remove()
    this.setContext()
  }

  setContext(){
    /*D3 logic goes here
     . add svg element
     . add gradient if desired
     . set the scales
     . normalize data
     . add the points
     . add the axes
     . add the axis labels
    */
    let height = this.props.height - this.props.margin.top - this.props.margin.bottom
    let width = this.props.width - this.props.margin.left - this.props.margin.right

    let div = d3.select(this.refs.tooltip)
      .append('div')
      .attr("class","tooltip")
      .style("opacity", "0")

    let x = d3.scaleLinear().range([0,width])
    let y = d3.scaleLinear().range([height, 0])

    let context = d3.select(this.refs.nflchart)
      .append('svg')
      .attr('height', this.props.height)
      .attr('width', this.props.width)
      .append('g')
      .attr( 'transform', 'translate(' + this.props.margin.left + ', ' + this.props.margin.top + ')')


//<pattern id="tile-ww" patternUnits="userSpaceOnUse" width="6" height="6">
//<image xlink:href="/images/tile-worldwide.png" x="0" y="0" width="6" height="6"></image></pattern>

    let defs = context.append("defs")
    let nwe = defs.append("pattern")
          .attr("id", "NWE")
          //xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="images/story-34.jpg" width="108" height="150"
          //https://upload.wikimedia.org/wikipedia/en/b/b9/New_England_Patriots_logo.svg
        nwe.append("image")
         .attr("xlink:href", "../icons/NWE_logo.svg")
         .attr('height','50')
         .attr('width', '50')

    d3.csv('data.csv', function(error, data){
      if (error){
        console.log('A data error occurred')
      }

      //format required fields as numeric
      data.forEach( function(d){
        d.WINS = +d.WINS
        d.DEF_AVG_PTS = +d.DEF_AVG_PTS
        d.OFF_AVG_PTS = +d.DEF_AVG_PTS
      })

      x.domain([0, 16])
      y.domain([0, d3.max( data, function(d){ return d.OFF_AVG_PTS})])

      context.selectAll('dot')
        .data(data)
        .enter().append('circle')
        .attr('r', 50)
        .attr('cx', function(d){ return x(d.WINS) })
        .attr('cy', function(d){ return y(d.OFF_AVG_PTS)})
        .attr('fill', 'url("#NWE")')
        .on("mouseover", function(d) {
           div.transition()
           .duration(200)
           .style("opacity", .9);
           div.html("Team: " + d.Tm + "<br/>Wins: " + d.WINS + "<br/>Pts/Drv: " + d.OFF_AVG_PTS)
           .style("left", (d3.event.pageX-80) + "px")
           .style("top", (d3.event.pageY - 28) + "px");
         })
       .on("mouseout", function(d) {
         div.transition()
         .duration(500)
         .style("opacity", 0);
       });

    })

  }

  render(){
    return (
      <div>
        <h1 className="title">NFL Visualization</h1>
        <h3 className="title">sub header for visualization</h3>
        <div ref="nflchart"></div>
        <div ref="tooltip"></div>
      </div>
    )
  }
}



